<template>
	<img alt="Vue logo" src="./assets/logo.png" />
	<h2>Lesson 65 Composables</h2>
	<h3>Composables replace Mixins Vue version 2- Codevolution lesson 50</h3>
	<h3>Functions stored in an external src folder javascript file useCounter</h3>
	<h3 style="text-align: left">--------- useCounter.js ------------</h3>
	<div style="text-align: left">
		<pre><code>
    import { ref } from "vue";

    export default function useCounter() {
      const count = ref(0);

      function incrementCount() {
         count.value += 1;
      } //endincrementCount

      return {
         count,
         incrementCount,
      }; //endreturn
    } //enduseCounter
</code></pre>
	</div>
	<h3 style="text-align: left">
		----------- useCounterBigger.js -------------
	</h3>
	<div style="text-align: left">
		<pre><code>
    import { ref } from "vue";

    export default function useCounterBigger(initialCount = 0, stepSize = 1) {
      const countBig = ref(initialCount);

    function incrementCountBig() {
       countBig.value += stepSize;
    } //endincrementCountBig

    return {
       countBig,
       incrementCountBig,
    }; //endreturnBig
   } //enduseCounterBigger    
    </code></pre>
	</div>
	<h3 style="text-align: left">
		-----------------------------------------------
	</h3>
