import { ref } from "vue";

export default function useCounterBigger(initialCount = 0, stepSize = 1) {
	const countBig = ref(initialCount);

	function incrementCountBig() {
		countBig.value += stepSize;
	} //endincrementCountBig

	return {
		countBig,
		incrementCountBig,
	}; //endreturnBig
} //enduseCounterBigger
